.btn-classic {
    background-color: $clr1;
    border: 2px solid rgba(0, 0, 0, 0.286);
    color: $clr4;
    border-radius: 0.3em;
    font-family: logo;
    font-weight: 100;
    text-shadow: 1px solid black;
    font-size: .7em;
    text-shadow: .3px 0px 1.5px black;
    letter-spacing: 0.1em;
    cursor: pointer;

    >* {
        color: $clr4;

        &:hover {
            background-color: $clr1;
            color: $clr3;

        }
    }

    &:hover {
        background-color: $clr1;
        color: $clr3;

    }
}